import React, { useState, useEffect, useCallback } from 'react';
import initializeData from '../utils/InitializeData';
import Slider from './Slider';
// #e3964a, #76a947
const ROICalculator = () => {

    const [mode, setMode] = useState('dark');
    const [patientsPerWeek, setPatientsPerWeek] = useState(50);
    const [screenedPct, setScreenedPct] = useState(10);
    const [convertedPct, setConvertedPct] = useState(10);
    const [ultraPct, setUltraPct] = useState(5);
    const [compressPct, setCompressPct] = useState(5);
    const [duoPct, setDuoPct] = useState(2);
    const [scrubPct, setScrubPct] = useState(5);

    const darkToggle = () => setMode((mode === 'lite') ? 'dark' : 'lite');
    const background = () => (mode === 'lite') ? 'bg-white' : null;
    const headerBackground = () => (mode === 'lite') ? 'bg-black' : null;
    const containerBackground = () => (mode === 'lite') ? 'bg-tinted color-lite' : null;
    const containedHeaderBackground = () => (mode === 'lite') ? 'bg-tintedMedium' : null;
    const toggleBackground = () => (mode === 'lite') ? 'bg-dark color-yellow' : null;
    const color = () => (mode === 'lite') ? 'color-yellow' : 'color-yellow';
    const modeIcon = () => (mode === 'lite') ? '' : '';
    const toggleTitle = () => (mode === 'lite') ? 'dark mode' : 'lite mode';

    const debounce = (func, delay) => {
        let timer;
        return (...args) => {
            clearTimeout(timer);
            timer = setTimeout(() => func(...args), delay);
        };
    };

    const debouncedSetPatientsPerWeek = useCallback(debounce(setPatientsPerWeek, 200), []);
    const debouncedSetScreenedPct = useCallback(debounce(setScreenedPct, 200), []);
    const debouncedSetConvertedPct = useCallback(debounce(setConvertedPct, 200), []);
    const debouncedSetUltraPct = useCallback(debounce(setUltraPct, 200), []);
    const debouncedSetCompressPct = useCallback(debounce(setCompressPct, 200), []);
    const debouncedSetDuoPct = useCallback(debounce(setDuoPct, 200), []);
    const debouncedSetScrubPct = useCallback(debounce(setScrubPct, 200), []);

    const calculateProfit = () => {
        const screened = (patientsPerWeek * screenedPct) / 100;
        const screenedProfit = screened * 25;

        const converted = (patientsPerWeek * convertedPct) / 100;
        const treatmentProfit = converted * 400;

        const ultra = (patientsPerWeek * ultraPct) / 100;
        const ultraProfit = ultra * 53;

        const compress = (patientsPerWeek * compressPct) / 100;
        const compressProfit = compress * 20;

        const duo = (patientsPerWeek * duoPct) / 100;
        const duoProfit = duo * 50;

        const scrub = (patientsPerWeek * scrubPct) / 100;
        const scrubProfit = scrub * 10;

        const weeklyProfit = screenedProfit + treatmentProfit + ultraProfit + compressProfit + duoProfit + scrubProfit;
        const monthlyProfit = weeklyProfit * 4;


        return {
            screenedProfit,
            treatmentProfit,
            ultraProfit,
            compressProfit,
            duoProfit,
            scrubProfit,
            weeklyProfit,
            monthlyProfit
        };
    };

    const profit = calculateProfit();

    const EYE_EXAM_REVENUE = 105;
    const totalAnnualProfit = profit.monthlyProfit * 12;
    const eyeExamsRequired = Math.ceil(totalAnnualProfit / EYE_EXAM_REVENUE);

    const formatCurrency = (amount) => {
        return `$${Number(amount).toLocaleString('en-US', { minimumFractionDigits: 0 })}`;
    };
    
    useEffect(() => {
        console.log(`patientsPerWeekly:<br/> ${formatCurrency(patientsPerWeek)}`);
    }, [patientsPerWeek]);

    useEffect(() => {
        const localMode = initializeData('mode', 'dark');
        setMode(localMode);
    }, []);

    return (
        <div className={`${background()}`}>
            {/*<div title={`${toggleTitle()}`} className='containerBox button width-50 rt-0' onClick={darkToggle}>{modeIcon()}</div>*/}
            <div className={`containerDetail brdr-ROIOrange mb-1 p-20 color-roiOrange columnLeftAlign ${headerBackground()}`}>OcuSci Dry Eye ROI Calculator</div>
            <div class='containerDetail brdr-ROIGreen mb-1 mt-1'>
                <Slider className='' label='Weekly Dry Eye Patients' value={patientsPerWeek} onChange={debouncedSetPatientsPerWeek} min={0} max={250} />
            </div>
            <div className='containerDetail brdr-ROIGreen mb-1 mt-1'>
                <div className='containerBox color-roiGreen columnLeftAlign'>
                    Meibovue Screening Profit:
                </div>
                <Slider className='' label='% Screened for MGD (Meibovue)' value={screenedPct} onChange={debouncedSetScreenedPct} />
                <div className='containerBox columnLeftAlign flexContainer'>
                    <div className='columnCenterAlign flex3Column size15'>
                        Weekly:<br /> {formatCurrency(profit.screenedProfit.toFixed(2))}
                    </div>
                    <div className='columnCenterAlign flex3Column size15'>
                        Monthly:<br /> {formatCurrency(profit.screenedProfit.toFixed(2) * 4)}
                    </div>
                    <div className='columnCenterAlign flex3Column size15'>
                        Yearly:<br /> {formatCurrency(Number((profit.screenedProfit.toFixed(2) * 4) * 12).toFixed(2))}
                    </div>
                </div>
                <div className='containerBox color-roiGreen columnLeftAlign'>
                    MGrx Treatment Profit:
                </div>
                <Slider className='' label='% Converted to MGrx Treatment' value={convertedPct} onChange={debouncedSetConvertedPct} />
                <div className='containerBox columnLeftAlign flexContainer'>
                    <div className='columnCenterAlign flex3Column size15'>
                        Weekly:<br /> {formatCurrency(profit.treatmentProfit.toFixed(2))}
                    </div>
                    <div className='columnCenterAlign flex3Column size15'>
                        Monthly:<br /> {formatCurrency(profit.treatmentProfit.toFixed(2) * 4)}
                    </div>
                    <div className='columnCenterAlign flex3Column size15'>
                        Yearly:<br /> {formatCurrency(Number((profit.treatmentProfit.toFixed(2) * 4) * 12).toFixed(2))}
                    </div>
                </div>
            </div>
            <div className='containerDetail brdr-ROIGreen mb-1 mt-1'>

                <div className='containerBox color-roiGreen columnLeftAlign'>
                    Ultra Dry Eye Profit:
                </div>
                <Slider className='' label='% Buying Ultra Dry Eye Supplement' value={ultraPct} onChange={debouncedSetUltraPct} />
                <div className='containerBox columnLeftAlign flexContainer'>
                    <div className='columnCenterAlign flex3Column size15'>
                        Weekly:<br /> {formatCurrency(profit.ultraProfit.toFixed(2))}
                    </div>
                    <div className='columnCenterAlign flex3Column size15'>
                        Monthly:<br /> {formatCurrency(profit.ultraProfit.toFixed(2) * 4)}
                    </div>
                    <div className='columnCenterAlign flex3Column size15'>
                        Yearly:<br /> {formatCurrency(Number((profit.ultraProfit.toFixed(2) * 4) * 12).toFixed(2))}
                    </div>
                </div>
            </div>
            <div className='containerDetail brdr-ROIGreen mb-1 mt-1'>
                <div className='containerBox color-roiGreen columnLeftAlign'>
                    Microwave Compress Profit:
                </div>
                <Slider className='' label='% Buying Microwave Compress' value={compressPct} onChange={debouncedSetCompressPct} />
                <div className='containerBox columnLeftAlign flexContainer'>
                    <div className='columnCenterAlign flex3Column size15'>
                        Weekly:<br /> {formatCurrency(profit.compressProfit.toFixed(2))}
                    </div>
                    <div className='columnCenterAlign flex3Column size15'>
                        Monthly:<br /> {formatCurrency(profit.compressProfit.toFixed(2) * 4)}
                    </div>
                    <div className='columnCenterAlign flex3Column size15'>
                        Yearly:<br /> {formatCurrency(Number((profit.compressProfit.toFixed(2) * 4) * 12).toFixed(2))}
                    </div>
                </div>
            </div>
            <div className='containerDetail brdr-ROIGreen mb-1 mt-1'>
                <div className='containerBox color-roiGreen columnLeftAlign'>
                    Duo USB Compress Profit:
                </div>
                <Slider className='' label='% Buying Duo USB Compress' value={duoPct} onChange={debouncedSetDuoPct} />
                <div className='containerBox columnLeftAlign flexContainer'>
                    <div className='columnCenterAlign flex3Column size15'>
                        Weekly:<br /> {formatCurrency(profit.duoProfit.toFixed(2))}
                    </div>
                    <div className='columnCenterAlign flex3Column size15'>
                        Monthly:<br /> {formatCurrency(profit.duoProfit.toFixed(2) * 4)}
                    </div>
                    <div className='columnCenterAlign flex3Column size15'>
                        Yearly:<br /> {formatCurrency(Number((profit.duoProfit * 4) * 12).toFixed(2))}
                    </div>
                </div>
            </div>
            <div className={`containerDetail brdr-ROIGreen mb-1 mt-1 ${containedHeaderBackground()}`}>
                <div className='containerBox color-roiGreen columnLeftAlign'>
                    Lid Scrub Profit:
                </div>
                <Slider className='' label='% Buying Lid Scrub' value={scrubPct} onChange={debouncedSetScrubPct} />
                <div className='containerBox columnLeftAlign flexContainer'>
                    <div className='columnCenterAlign flex3Column size15'>
                        Weekly:<br /> {formatCurrency(profit.scrubProfit.toFixed(2))}
                    </div>
                    <div className='columnCenterAlign flex3Column size15'>
                        Monthly:<br /> {formatCurrency(profit.scrubProfit.toFixed(2) * 4)}
                    </div>
                    <div className='columnCenterAlign flex3Column size15'>
                        Yearly:<br /> {formatCurrency(Number((profit.scrubProfit * 4) * 12).toFixed(2))}
                    </div>
                </div>
            </div>
            <div className='containerDetail brdr-ROIGreen mb-1 mt-1'>
                <div className={`containerDetail pl-10 pb-20 pt-20 columnLeftAlign color-roiGreen`}>Profit Summary</div>      
                <div className='containerDetail m-1'>
                    <div className={`pl-5 pt-10 pb-10 color-roiOrange columnLeftAlign ${headerBackground()}`}>
                        Total Estimated Profit:
                    </div>
                    <div className='containerDetail columnLeftAlign flexContainer'>
                        <div className='m-1 columnLeftAlign flex3Column size15 color-roiGreen'>
                            Weekly:<br/> {formatCurrency(profit.weeklyProfit.toFixed(2))}
                        </div>
                        <div className='m-1 columnLeftAlign flex3Column size15 color-roiGreen'>
                            Monthly:<br/> {formatCurrency(profit.monthlyProfit.toFixed(2))}
                        </div>
                        <div className='m-1 columnLeftAlign flex3Column size15 color-roiGreen'>
                            Yearly:<br/> {formatCurrency(Number((profit.monthlyProfit.toFixed(2)) * 12).toFixed(2))}
                        </div>
                    </div>
                    <div className='pl-10 pr-10 pb-10 pt-20 columnLeftAlign size15'>
                        Approximately {formatCurrency(Number(eyeExamsRequired).toFixed(2)).toString().replace('$', '')} eye exams per year
                        at ${EYE_EXAM_REVENUE.toLocaleString()}
                    </div>
                </div>
            </div>
            <div className={`ht-400`}></div>
        </div>
    );
};

export default ROICalculator;